import React, { useMemo, useState } from 'react';
import { useAppStore } from '@/store/useAppStore';
import { cn } from '@/lib/utils';
import { Side, Block } from '@/types';
import { X } from 'lucide-react';

// Create an array [1, ..., 100]
const GRID_CELLS = Array.from({ length: 100 }, (_, i) => i + 1);

const NORTH_LAYOUT: (Block | null)[][] = [
  ['', 'G', 'D', 'A'],
  ['', 'H', 'E', 'B'],
  ['', 'I', 'F', 'C'],
];

const SOUTH_LAYOUT: (Block | null)[][] = [
  ['J', null, null, null],
  ['', 'G', 'D', 'A'],
  ['', 'H', 'E', 'B'],
  ['', 'I', 'F', 'C'],
];

interface BlockViewProps {
  side: Side;
  layout: (Block | null)[][];
  activeBlocks: Set<string>;
  filteredLines: any[];
  hasSearched: boolean;
  onBlockClick: (block: Block, lines: any[]) => void;
  ignoreSideFilter?: boolean;
}

const BlockView: React.FC<BlockViewProps> = ({ side, layout, activeBlocks, filteredLines, hasSearched, onBlockClick, ignoreSideFilter = false }) => {
  return (
    <div className="flex flex-col items-center">
      <div className="flex flex-col gap-1">
        {layout.map((row, rowIndex) => (
          <div key={rowIndex} className="flex gap-1">
            {row.map((block, colIndex) => {
              if (!block) return <div key={colIndex} className="w-12 h-12 md:w-16 md:h-16" />; // Spacer
              
              // If ignoreSideFilter is true, activeBlocks contains just block names ("A").
              // Otherwise it contains "Side-Block" ("-A").
              const isActive = ignoreSideFilter 
                ? activeBlocks.has(block)
                : activeBlocks.has(`${side}-${block}`);
              
              if (!isActive) {
                // Render light gray block for non-active blocks
                return (
                  <div
                    key={block}
                    className="w-12 h-12 md:w-16 md:h-16 border-2 border-slate-800 bg-slate-900/30 text-slate-700 flex items-center justify-center text-xs md:text-sm font-bold rounded-lg select-none"
                  >
                    {block}
                  </div>
                );
              }

              // Filter lines for this block
              // If ignoreSideFilter is true, we want all lines in this block regardless of side
              const linesInBlock = filteredLines.filter(l => 
                (ignoreSideFilter ? true : l.side === side) && 
                l.block === block
              );
              
              let displayText = block;
              
              if (hasSearched && linesInBlock.length > 0) {
                  // If we have lines, try to find a block_core value
                  // Priority 1: Display block_core if available
                  const lineWithBlockCore = linesInBlock.find(l => l.block_core);
                  if (lineWithBlockCore && lineWithBlockCore.block_core) {
                      displayText = lineWithBlockCore.block_core;
                  } else {
                      // Priority 2: Display core_number of the first matching line
                      displayText = linesInBlock[0].core_number;
                  }
              }

              return (
                <div
                  key={block}
                  onClick={() => onBlockClick(block, linesInBlock)}
                  className={cn(
                    "w-12 h-12 md:w-16 md:h-16 border-2 flex items-center justify-center text-xs md:text-sm font-bold rounded-lg transition-all duration-200 select-none break-all text-center leading-tight p-0.5 cursor-pointer active:scale-95",
                    isActive
                      ? "border-blue-500 bg-blue-900/30 text-blue-400 shadow-[0_0_15px_rgba(59,130,246,0.5)] hover:bg-blue-800/50"
                      : "border-slate-700 bg-slate-800 text-slate-500"
                  )}
                >
                  {displayText}
                  {isActive && (
                    <div className="absolute w-1.5 h-1.5 bg-red-500 rounded-full top-1 right-1 animate-pulse shadow-[0_0_8px_rgba(239,68,68,0.8)]" />
                  )}
                </div>
              );
            })}
          </div>
        ))}
      </div>
    </div>
  );
};

interface CoreGridProps {
  title: string;
  colorClass: string;
  data: any[];
  selectedCore: { side?: string; block?: string; core: string; line_type?: string } | null;
  lineType: string;
  onSelect: (lineType: string, core: string) => void;
}

const CoreGrid: React.FC<CoreGridProps> = ({ title, colorClass, data, selectedCore, lineType, onSelect }) => {
  const hasData = (cellNumber: number) => {
    return data.some((line) => line.core_number === String(cellNumber));
  };

  const isSelected = (cellNumber: number) => {
    return (
      selectedCore?.line_type === lineType &&
      selectedCore?.core === String(cellNumber)
    );
  };

  return (
    <div className="flex flex-col items-center bg-slate-900 p-2 rounded-lg shadow-md border border-slate-800 w-full overflow-hidden">
      <h4 className="text-sm font-bold mb-2 flex items-center gap-2 text-slate-200">
        <span className={cn("w-1.5 h-1.5 rounded-full shadow-[0_0_8px_currentColor]", colorClass)} />
        {title}
      </h4>
      <div className="overflow-x-auto w-full pb-1 scrollbar-thin scrollbar-thumb-slate-700 scrollbar-track-transparent">
        <div className="grid grid-cols-10 gap-1 min-w-fit mx-auto p-0.5">
        {GRID_CELLS.map((num) => {
          const has = hasData(num);
          const selected = isSelected(num);
          return (
            <div
              key={num}
              onClick={() => {
                if (selected) {
                  onSelect('', ''); 
                } else if (has) {
                  onSelect(lineType, String(num));
                }
              }}
              className={cn(
                "relative w-7 h-7 md:w-8 md:h-8 border flex items-center justify-center cursor-pointer transition-all duration-100 text-[10px] md:text-xs select-none rounded active:scale-90",
                selected
                  ? "border-blue-400 bg-blue-600 text-white z-10 ring-1 ring-blue-400 shadow-[0_0_10px_rgba(59,130,246,0.6)]"
                  : "border-slate-700 bg-slate-800 hover:bg-slate-700",
                !has && !selected && "opacity-30 bg-slate-900 border-slate-800 text-slate-600 cursor-default",
                has && !selected && "text-slate-200 hover:border-slate-500"
              )}
            >
              <span className="font-medium">{num}</span>
              {has && !selected && (
                <div className="absolute top-0 right-0 w-1.5 h-1.5 bg-red-500 rounded-full -mr-0.5 -mt-0.5 shadow-[0_0_5px_rgba(239,68,68,0.8)]" />
              )}
            </div>
          );
        })}
        </div>
      </div>
    </div>
  );
};

export const DistributionFrame: React.FC = () => {
  const { filteredLines, hasSearched, selectedCore, setSelectedCore } = useAppStore();
  const [selectedBlockDetail, setSelectedBlockDetail] = useState<{ block: Block; lines: any[]; side?: Side } | null>(null);

  // Group lines by line_type (backward compatibility: North=Inner, South=Outer if line_type is missing)
  const innerLines = useMemo(() => filteredLines.filter(l => l.line_type === '' || (!l.line_type && l.side === '')), [filteredLines]);
  const outerLines = useMemo(() => filteredLines.filter(l => l.line_type === '' || (!l.line_type && l.side === '')), [filteredLines]);

  // Calculate active blocks for highlighting in BlockView
  // We use block names ("A", "B") instead of "Side-Block" because we want to merge North/South in the display
  const innerActiveBlocks = useMemo(() => {
    const blocks = new Set<string>();
    innerLines.forEach(l => blocks.add(l.block));
    return blocks;
  }, [innerLines]);

  const outerActiveBlocks = useMemo(() => {
    const blocks = new Set<string>();
    outerLines.forEach(l => blocks.add(l.block));
    return blocks;
  }, [outerLines]);

  const handleCellSelect = (lineType: string, core: string) => {
    if (!lineType) {
        setSelectedCore(null, null, null);
    } else {
        setSelectedCore(null, null, core, lineType);
    }
  };

  const handleBlockClick = (block: Block, lines: any[]) => {
      setSelectedBlockDetail({ block, lines });
  };

  if (innerLines.length === 0 && outerLines.length === 0) {
    return (
      <div className="text-center py-12 text-slate-500">
        
      </div>
    );
  }

  return (
    <div className="space-y-4 animate-in fade-in duration-500 p-0 md:p-2 relative">
      
      {/* Block Detail Modal */}
      {selectedBlockDetail && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/70 p-4 backdrop-blur-sm animate-in fade-in duration-200" onClick={() => setSelectedBlockDetail(null)}>
          <div className="bg-slate-900 rounded-lg shadow-2xl w-full max-w-sm border border-slate-800 p-4 relative" onClick={e => e.stopPropagation()}>
            <button
              onClick={() => setSelectedBlockDetail(null)}
              className="absolute top-3 right-3 text-slate-400 hover:text-slate-200"
            >
              <X className="w-5 h-5" />
            </button>
            <h3 className="text-lg font-bold text-slate-100 mb-4 border-b border-slate-800 pb-2">
                {selectedBlockDetail.block} 
            </h3>
            <div className="max-h-[60vh] overflow-y-auto space-y-2 pr-2 scrollbar-thin scrollbar-thumb-slate-700">
              {selectedBlockDetail.lines.map((line, idx) => (
                <div key={idx} className="bg-slate-800/50 p-2 rounded border border-slate-700/50 hover:bg-slate-800 transition-colors">
                   <div className="flex justify-between items-center mb-1">
                      <span className="text-blue-400 font-mono font-bold text-sm">{line.block_core || `${line.block}-${line.core_number}`}</span>
                      <span className={cn("text-xs px-1.5 py-0.5 rounded", line.side === '' ? "bg-blue-900/50 text-blue-300" : "bg-green-900/50 text-green-300")}>
                        {line.side}
                      </span>
                   </div>
                   <div className="flex justify-between items-center text-xs text-slate-500 mb-1">
                        <span>{line.core_number} </span>
                   </div>
                   <div className="text-sm text-slate-300 truncate">{line.company_name}</div>
                   <div className="text-xs text-slate-400 truncate">{line.phone_number}</div>
                </div>
              ))}
            </div>
          </div>
        </div>
      )}

      {/* Inner Lines Section */}
      <section className="space-y-4 bg-transparent p-0">
          <div className="flex flex-col md:flex-row gap-4 items-center md:items-start">
             <div className="flex-shrink-0 text-center md:text-left space-y-4">
               <h3 className="text-base font-bold mb-3 flex items-center gap-2 text-slate-100 justify-center md:justify-start">
                 <span className="w-3 h-3 bg-blue-500 rounded-full shadow-[0_0_10px_rgba(59,130,246,0.8)]"></span>
                 
               </h3>
               
               {/* North Layout for Inner */}
               <div className="bg-slate-800/30 p-2 rounded-lg border border-slate-700/50">
                 <div className="text-xs text-slate-400 mb-1 text-center font-bold"></div>
                 <BlockView 
                   side="" 
                   layout={NORTH_LAYOUT} 
                   activeBlocks={innerActiveBlocks} 
                   filteredLines={innerLines}
                   hasSearched={hasSearched}
                   onBlockClick={handleBlockClick}
                   ignoreSideFilter={false}
                 />
               </div>

               {/* South Layout for Inner */}
               <div className="bg-slate-800/30 p-2 rounded-lg border border-slate-700/50">
                 <div className="text-xs text-slate-400 mb-1 text-center font-bold"></div>
                 <BlockView 
                   side="" 
                   layout={SOUTH_LAYOUT} 
                   activeBlocks={innerActiveBlocks} 
                   filteredLines={innerLines}
                   hasSearched={hasSearched}
                   onBlockClick={handleBlockClick}
                   ignoreSideFilter={false}
                 />
               </div>
             </div>
             
             {/* Inner Grid (Single) */}
             <div className="flex-1 w-full">
               {innerLines.length > 0 ? (
                  <CoreGrid
                    title=""
                    colorClass="bg-blue-500 text-blue-500"
                    data={innerLines}
                    selectedCore={selectedCore}
                    lineType=""
                    onSelect={handleCellSelect}
                  />
               ) : (
                 <div className="text-slate-500 text-sm py-8 text-center md:text-left border-2 border-dashed border-slate-800 rounded-lg">
                   
                 </div>
               )}
             </div>
          </div>
      </section>

      {/* Outer Lines Section */}
      <section className="space-y-4 bg-transparent p-0">
          <div className="flex flex-col md:flex-row gap-4 items-center md:items-start">
             <div className="flex-shrink-0 text-center md:text-left space-y-4">
               <h3 className="text-base font-bold mb-3 flex items-center gap-2 text-slate-100 justify-center md:justify-start">
                 <span className="w-3 h-3 bg-green-500 rounded-full shadow-[0_0_10px_rgba(34,197,94,0.8)]"></span>
                 
               </h3>
               
               {/* North Layout for Outer */}
               <div className="bg-slate-800/30 p-2 rounded-lg border border-slate-700/50">
                 <div className="text-xs text-slate-400 mb-1 text-center font-bold"></div>
                 <BlockView 
                   side="" 
                   layout={NORTH_LAYOUT} 
                   activeBlocks={outerActiveBlocks} 
                   filteredLines={outerLines}
                   hasSearched={hasSearched}
                   onBlockClick={handleBlockClick}
                   ignoreSideFilter={false}
                 />
               </div>

               {/* South Layout for Outer */}
               <div className="bg-slate-800/30 p-2 rounded-lg border border-slate-700/50">
                 <div className="text-xs text-slate-400 mb-1 text-center font-bold"></div>
                 <BlockView 
                   side="" 
                   layout={SOUTH_LAYOUT} 
                   activeBlocks={outerActiveBlocks} 
                   filteredLines={outerLines}
                   hasSearched={hasSearched}
                   onBlockClick={handleBlockClick}
                   ignoreSideFilter={false}
                 />
               </div>
             </div>

             {/* Outer Grid (Single) */}
             <div className="flex-1 w-full">
               {outerLines.length > 0 ? (
                  <CoreGrid
                    title=""
                    colorClass="bg-green-500 text-green-500"
                    data={outerLines}
                    selectedCore={selectedCore}
                    lineType=""
                    onSelect={handleCellSelect}
                  />
               ) : (
                 <div className="text-slate-500 text-sm py-8 text-center md:text-left border-2 border-dashed border-slate-800 rounded-lg">
                    
                 </div>
               )}
             </div>
          </div>
      </section>

    </div>
  );
};
